"use client";

import {
  Box,
  IconButton,
  Typography,
  Drawer,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Modal,
  Slide,
  Button,
} from "@mui/material";
import styles from "./header.module.css";
import LocalFloristIcon from "@mui/icons-material/LocalFlorist";
import MenuIcon from "@mui/icons-material/Menu";
import HomeIcon from "@mui/icons-material/Home";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import ContactMailIcon from "@mui/icons-material/ContactMail";
import InfoIcon from "@mui/icons-material/Info";
import Link from "next/link";
import { useState } from "react";

const Header = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [aboutOpen, setAboutOpen] = useState(false);

  const menu = [
    { label: "Home", icon: <HomeIcon sx={{ fontSize: 50, p: 1 }} /> },
    { label: "Shop", icon: <ShoppingCartIcon sx={{ fontSize: 50, p: 1 }} /> },
    { label: "Contact", icon: <ContactMailIcon sx={{ fontSize: 50, p: 1 }} /> },
    { label: "About", icon: <InfoIcon sx={{ fontSize: 50, p: 1 }} /> },
  ];

  const handleMenuClick = (label: string) => {
    if (label === "About") {
      setAboutOpen(true);
    }
  };

  return (
    <Box className={styles.header}>
      {/* Logo & Mobile Title */}
      <Box sx={{ display: "flex", alignItems: "center", p: 1 }}>
        <LocalFloristIcon sx={{ fontSize: 60, p: 1 }} />
        <Link href="/" passHref legacyBehavior>
          <Box sx={{ display: { md: "none" }, p: 1 }}>
            <a
              style={{
                userSelect: "none",
                WebkitTapHighlightColor: "transparent",
                textDecoration: "none",
                color: "black",
                fontSize: "1.5rem",
                padding: 8,
                display: "inline-block",
              }}
            >
              NK's Flowers
            </a>
          </Box>
        </Link>
      </Box>

      {/* Center Title on Desktop */}
      <Box
        sx={{
          pt: 5,
          flexGrow: 1,
          textAlign: "center",
          display: { xs: "none", md: "block" },
        }}
      >
        <Link href="/">
          <Typography variant="h4" sx={{ fontFamily: "cursive" }}>
            NK's Flowers
          </Typography>
        </Link>
      </Box>

      {/* Desktop Menu */}
      <Box sx={{ pt: 5, display: { xs: "none", md: "flex" }, gap: 2, p: 2 }}>
        {menu.map((item, i) => (
          <a
            key={i}
            href="#"
            className={styles.menuLink}
            onClick={(e) => {
              e.preventDefault();
              handleMenuClick(item.label);
            }}
          >
            {item.label}
          </a>
        ))}
      </Box>

      {/* Mobile Menu Icon */}
      <IconButton
        disableRipple
        disableFocusRipple
        onClick={() => setDrawerOpen(true)}
        sx={{ display: { xs: "flex", md: "none" } }}
      >
        <MenuIcon sx={{ fontSize: 50, color: "black", p: 1 }} />
      </IconButton>

      {/* Drawer */}
      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <Box sx={{ width: 250, p: 2 }}>
          <Typography variant="h6" sx={{ mb: 2 }}>
            Menu
          </Typography>
          <List>
            {menu.map((item, index) => (
              <ListItem
                key={index}
                onClick={() => {
                  setDrawerOpen(false);
                  handleMenuClick(item.label);
                }}
                
              >
                <ListItemIcon>{item.icon}</ListItemIcon>
                <ListItemText primary={item.label} sx={{ p: 1 }} />
              </ListItem>
            ))}
          </List>
        </Box>
      </Drawer>

      {/* About Popup */}
      <Modal open={aboutOpen} onClose={() => setAboutOpen(false)}>
        <Slide direction="up" in={aboutOpen} mountOnEnter unmountOnExit timeout={{ enter: 400, exit: 300 }}>
          <Box
            sx={{
              position: "fixed",
              bottom: 0,
              left: 0,
              right: 0,
              height: "80vh",
              bgcolor: "white",
              zIndex: 1300,
              p: 4,
              overflowY: "auto",
              borderTopLeftRadius: 20,
              borderTopRightRadius: 20,
              boxShadow: 6,
              backgroundColor: "#FFC0CB"
            }}
          >
            <Typography variant="h4" gutterBottom>
              About NK's Flowers
            </Typography>
            <Typography variant="body1">
              We specialize in elegant, hand-picked floral arrangements for every occasion.
              <br /><br />
              Our mission is to bring natural beauty and joy to your life through carefully crafted floral designs.
              <br /><br />
              Thank you for visiting us!
            </Typography>
            <Box sx={{ mt: 4 }}>
              <Button
                variant="contained"
                color="primary"
                onClick={() => setAboutOpen(false)}
                sx={{
                  px: 4,
                  py: 1.5,
                  fontSize: "1rem",
                  borderRadius: "8px",
                  textTransform: "none",
                  boxShadow: 2,
                  ":hover": {
                    backgroundColor: "#1976d2",
                  },
                }}
              >
                Close
              </Button>
            </Box>
          </Box>
        </Slide>
      </Modal>
    </Box>
  );
};

export default Header;
